body {
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #2f2f2f;
}

body::-webkit-scrollbar {
    width: 3px;
}

body::-webkit-scrollbar-thumb {
    background-color: #6a6a6a;
    transition: background-color .5s ease-out;
}

body::-webkit-scrollbar-track-piece {
    background-color: #2a2a2a;
    transition: background-color .5s ease-out;
}

.menu-bar-active i:nth-child(2) {
    opacity: 0 !important;
}

.menu-bar-active i:nth-child(1) {
    transform: translate(0, 10px) rotate(45deg) scale(1.5) !important;
}

.menu-bar-active i:nth-child(3) {
    transform: translate(0, -10px) rotate(-45deg) scale(1.5) !important;
}
